import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgxCcComponent } from './ngx-cc.component';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { NumberOnlyDirective } from './directives/number-only/number-only.directive';
import { CcDateComponent } from './cc-date/cc-date.component';
import { FormatDateDirective } from './directives/format-date/format-date.directive';
import { CcCvvComponent } from './cc-cvv/cc-cvv.component';
import { CcConfig, CC_CARD_ICONS_TOKEN } from './ngx-cc.config';
import { externalCardIcons } from './ngx-cc.icons';

@NgModule({
  declarations: [
    NgxCcComponent,
    NumberOnlyDirective,
    CcDateComponent,
    FormatDateDirective,
    CcCvvComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    MatInputModule,
    MatFormFieldModule
  ],
  exports: [
    NgxCcComponent,
    CcDateComponent,
    CcCvvComponent,
    NumberOnlyDirective,
    FormatDateDirective
  ],
})
export class NgxCcModule {
  static forRoot(config: CcConfig): ModuleWithProviders<NgxCcModule> {
    return {
      ngModule: NgxCcModule,
      providers: [
        {
          provide: CC_CARD_ICONS_TOKEN,
          useValue: { ...externalCardIcons, ...config.cardIcons },
        }
      ]
    };
  }
}
